<template>
  <div>
    <LayoutBox one="用户管理" two="修改密码">
      <el-form ref="ruleForm" :model="form" :rules="rules" size="small" label-width="100px" style="width:400px">
        <el-form-item label="手机号：" prop="phoneNum">
          <el-input v-model="form.phoneNum"></el-input>
        </el-form-item>
        <el-form-item label="验证码：" prop="verificationCode">
          <el-input v-model="form.verificationCode">
            <template slot="append">
              <el-button v-if="countdown" style="width: 120px; cursor: not-allowed">{{ countdown }}秒后重发</el-button>
              <el-button v-else style="width: 120px" @click="getVerificationCode">获取验证码</el-button>
            </template>
          </el-input>
        </el-form-item>
        <el-form-item label="新密码：" prop="newPassword">
          <el-input v-model="form.newPassword"></el-input>
        </el-form-item>
        <el-form-item label="确认密码：" prop="confirmPassword">
          <el-input v-model="form.confirmPassword"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">确定</el-button>
        </el-form-item>
      </el-form>
    </LayoutBox>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import { MIXINS_FORGETPASSWORD_API } from '@/mixins/api'
import { MIXINS_BASIC_FORMRULES } from '@/mixins/form'

export default {
  name: 'editPassword',

  components: {
    LayoutBox,
  },

  mixins: [
    MIXINS_FORGETPASSWORD_API,
    MIXINS_BASIC_FORMRULES,
  ],

  data () {
    return {
      form: {
        phoneNum: '', // 手机号
        newPassword: '', // 新密码
        confirmPassword: '', // 确认密码
        verificationCode: '', // 验证码
      },
      countdown: 0,
      rules: {
        phoneNum: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
        ],
        verificationCode: [
          { required: true, message: '请输入验证码', trigger: 'blur' },
        ],
        newPassword: [
          { required: true, message: '请输入新密码', trigger: 'blur' },
        ],
        confirmPassword: [
          { required: true, message: '请输入新密码', trigger: 'blur' },
          { validator: this.validatePass2, trigger: 'blur' },
        ]
      },
    }
  },

  methods: {
    // 获取验证码
    async getVerificationCode () {
      if (!(/^1[3456789]\d{9}$/.test(this.form.phoneNum))) {
        return this.$message.error('手机号码有误，请重填')
      }
      const data = {
        phoneNum: this.form.phoneNum,
        type: 3,
      }
      const res = await this.$logisticsHttp.post(this.$api.smscode, data)
      if (!res.data.success) return this.$message.error(res.data.msg)
      this.countdown = 60
      const interval = setInterval(() => {
        this.countdown--
        if (this.countdown === 0) {
          clearInterval(interval)
        }
      }, 1000)
    },
    // 点击确认
    onSubmit () {
      if (this._ruleForm('ruleForm')) {
        const data = {
          username: this.form.phoneNum,
          ...this.form,
        }
        this.forgetPassword(data).then((res) => {
          console.log(res)
          if (res.data.success) {
            this.$message.success('修改成功')
          } else {
            this.$message.error(res.data.msg)
          }
        })
      }
    },
    // 验证确认密码是否和新密码一样
    validatePass2 (rule, value, callback) {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.form.newPassword) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
  }

}
</script>

<style lang="scss" scoped>

</style>
